h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  margin-bottom: $headings-margin-bottom;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  color: inherit;
}

@each $size, $value in $heading-sizes {
  #{$size},
  .#{$size} {
    font-size: $value;
  }
}